.app {
  background: #222;
  color: whitesmoke;
  min-height: 100vh;
  overflow-x: hidden;
}

h1 {
  font-weight: 400;
  font-size: 40px;
  border-bottom: solid;
  text-align: center;
  padding-bottom: 12px;
}

h2 {
  font-weight: 300;
  font-size: 18px;
  margin: 0;
}

p {
  margin: 0;
  font-size: 14px;
  color: darkgrey;
}

.post {
  display: -ms-flexbox;
  display: flex;
  -webkit-animation: fade-in 0.6s;
          animation: fade-in 0.6s;
  margin-bottom: 12px;
}

.postDetails {
  margin: 0 12px;
}

.score {
  width: 70px;
  max-height: 100px;
  -ms-flex-negative: 0;
      flex-shrink: 0;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  font-size: 16px;
}

.score::before {
  content: "▲";
  position: absolute;
  -webkit-transform: translateY(-18px);
      -ms-transform: translateY(-18px);
          transform: translateY(-18px);
  color: darkgray;
}

.score::after {
  content: "▼";
  position: absolute;
  -webkit-transform: translateY(18px);
      -ms-transform: translateY(18px);
          transform: translateY(18px);
  color: grey;
}

.inline {
  display: inline;
}

.thumbnail {
  width: 100px;
  -o-object-fit: scale-down;
     object-fit: scale-down;
  -o-object-position: top;
     object-position: top;
  border-radius: 4px;
  height: 100%;
  -ms-flex-negative: 0;
      flex-shrink: 0;
}

.blankThumbnail {
  width: 100px;
  height: 100px;
  -ms-flex-negative: 0;
      flex-shrink: 0;
  border-radius: 4px;
  background: lightgrey;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  color: darkgrey;
}

.loaderContainer {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 12px;
}

.loader,
.loader:before,
.loader:after {
  border-radius: 50%;
  width: 15px;
  height: 15px;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
  -webkit-animation: loading 1.4s infinite ease-in-out;
          animation: loading 1.4s infinite ease-in-out;
}

.loader {
  color: lightgrey;
  font-size: 10px;
  margin: 22px auto;
  display: grid;
  position: relative;
  -webkit-animation-delay: -0.16s;
          animation-delay: -0.16s;
}

.loader:before,
.loader:after {
  content: '';
  position: absolute;
  top: 0;
}

.loader:before {
  left: -35px;
  -webkit-animation-delay: -0.32s;
          animation-delay: -0.32s;
}

.loader:after {
  left: 35px;
}

@-webkit-keyframes loading {
  0%,
  80%,
  100% {
    -webkit-box-shadow: 0 25px 0 -1.3em;
            box-shadow: 0 25px 0 -1.3em;
  }
  40% {
    -webkit-box-shadow: 0 25px 0 0;
            box-shadow: 0 25px 0 0;
  }
}

@keyframes loading {
  0%,
  80%,
  100% {
    -webkit-box-shadow: 0 25px 0 -1.3em;
            box-shadow: 0 25px 0 -1.3em;
  }
  40% {
    -webkit-box-shadow: 0 25px 0 0;
            box-shadow: 0 25px 0 0;
  }
}

@-webkit-keyframes fade-in {
  0% {
    opacity: 0;
    -webkit-transform: translateX(4px);
            transform: translateX(4px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

@keyframes fade-in {
  0% {
    opacity: 0;
    -webkit-transform: translateX(4px);
            transform: translateX(4px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



